  .description{
    z-index: 10;
    height: 90px;
    background-color:#ffffff;
    position: relative;
    top: -40px;
    width: 90%;
    margin: auto;
    border-radius: 10px;
    padding: 15px;
    box-shadow: 0 4px 8px -6px gray;
  }
  
  
  .description-title{
    font-family: 'Quicksand', sans-serif;
    font-weight: lighter;
  }
  
  .sub-info{
    font-family: 'Open sans', sans-serif;
    font-size: 0.75em;
  }
  
  
  .icon-easy{
    color: #ffd972;
  }
  
  .icon-cake{
    color: #c7eae4;
  
  }
  
  .icon-clock{
    color: #efa7a7;
  }
  
  .text-muted{
    font-size: 0.7em;
  }
  
  
  
.recipeImg{
    border-radius: 10px;
    background-color: #fcbcb8;
    width: 100px;
    height: 100px;
}

.card-sub-info{
    font-family: 'Open sans', sans-serif;
    font-size: 0.6em;
}

.info{
    padding-left:120px;
    border-radius: 10px;
    box-shadow: 4px 4px 10px -9px gray;
}



.full {
    width: 100%;
  }

.header-img{
    padding: 20px;
    height: 250px;
    background-color: #ffd972;
}

.recipe-description{
    z-index: 10;
    height: 90px;
    background-color:#ffffff;
    position: relative;
    top: -45px;
   
    margin: auto;
    border-radius: 20px;
    padding: 15px;
    padding-top: 25px;
}

.back{
    color: white;
}

.vdivide [class*='col-']:not(:last-child):after {
    background: #e0e0e0;
    width: 1px;
    content: "";
    display:block;
    position: absolute;
    top:0;
    bottom: 0;
    right: 0;
 
  }

  
